<template>
    <header>        
        <v-app-bar 
            app
            elevation="4"    
            inverted-scroll    
            padless            
        >        
            <v-btn
                icon
                large    
                x-large 
                href="#home"                
            >
                <v-icon>mdi-home</v-icon>                
            </v-btn>
            <v-toolbar-title class="mt-1">
                Portfolio
            </v-toolbar-title>            
            
            <v-row justify="center" class="d-none d-sm-block">
                <div class="text-center">
                    <v-btn                                           
                        text   
                        plain
                        href="#ddd"
                        @click="$vuetify.goTo('#ddd', options)"                        
                    > 
                        <v-icon left>
                            mdi-cube
                        </v-icon> 
                        Arts
                    </v-btn>   

                    <v-btn                                              
                        text
                        plain     
                        href="#script"                   
                        @click="$vuetify.goTo('#script', options)"
                    > 
                        <v-icon left>
                            mdi-script-outline
                        </v-icon>
                        Scripts
                    </v-btn>

                    <v-btn                                              
                        text
                        plain     
                        href="#projects"                   
                        @click="$vuetify.goTo('#projects', options)"
                    > 
                        <v-icon left>
                            mdi-controller-classic
                        </v-icon>
                        Projects
                    </v-btn>
                </div>
            </v-row>        

            <v-spacer class="d-block d-sm-none" />                         
            <HelpDialogue />             
            <v-btn class="me-2" icon @click="playerToggle">
                <v-icon>{{ (this.play) ? 'mdi-volume-off' : 'mdi-volume-high' }}</v-icon>
            </v-btn>      
        </v-app-bar>   
        <audio ref="audioPlayer">
            <source src="../assets/media/mindways_tot.ogg" type="audio/ogg" />
        </audio>
    </header>
</template>

<script>
    import HelpDialogue from './HelpDialogue.vue'    

    export default {
        name: 'NavBar',
        components: {
            HelpDialogue,
        },
        mounted() {
            this.audioPlayer = this.$refs['audioPlayer'];
            this.play        = this.audioPlayer.paused;
            this.audioPlayer.volume = 0.2;
            this.audioPlayer.loop = true;
        },
        data() {
            return {                 
                options: {
                    duration: 2000,
                    offset: 0,
                    easing: 'easeInOutCubic',
                },
                play: true
            }
        },
        methods: {
            playerToggle() {
                if (this.audioPlayer.paused) {
                    this.audioPlayer.play();
                }
                else {
                    this.audioPlayer.pause();
                }    
                this.play = this.audioPlayer.paused;
            }
        }
    };  
</script>